:root {
  --primary-white: #fff;
  --primary-orange: #f9ab00;
  --primary-dark: rgba(255, 255, 255, 0);
}

body {
  font-family: "Roboto Condensed", sans-serif;
}

nav {
  z-index: 3;
}

.bg-custom-2 {
  background-image: linear-gradient(90deg, black 0%, rgb(33, 37, 41) 100%);
  height: 10vh;
}

.logo {
  width: 4.25rem;
  object-fit: contain;
}

/* <Nav.Link> */
.nav-link {
  letter-spacing: 0.0375rem;
  cursor: pointer;
  text-transform: uppercase;
  text-decoration: none;
  color: white;
}

.nav-link:active,
.nav-link:root,
.nav-link:before {
  text-decoration: none;
  color: white;
}

.resume {
  margin-right: 0.75rem;
  margin-left: 0.75rem;
  letter-spacing: 0.0375rem;
  color: #fff !important;
  color: var(--primary-white) !important;
  text-transform: uppercase;
}

.navbar-brand {
  max-width: 17.25rem;
  padding: 0 !important;
  height: 10vh !important;
  display: flex;
  align-content: center;
  justify-content: center;
}

.navbar-nav > li > a {
  margin-right: 0.75rem;
  margin-left: 0.75rem;
  color: #fff !important;
  color: var(--primary-white) !important;
  border-top: 0.1875rem solid rgba(255, 255, 255, 0);
  border-top: 0.1875rem solid var(--primary-dark);
}

.navbar-nav > li > a.active,
.navbar-nav > li > a.active:focus {
  border-top: 0.1875rem solid #f9ab00;
  border-top: 0.1875rem solid var(--primary-orange);
}

.navbar-nav > li > a:hover {
  border-top: 0.1875rem solid #f9ab00;
  border-top: 0.1875rem solid var(--primary-orange);
}

.custom-nav-link {
  border-top: 0.1875rem solid rgba(255, 255, 255, 0) !important;
  border-top: 0.1875rem solid var(--primary-dark) !important;
}

.custom-nav-link:hover {
  border-top: 0.1875rem solid #f9ab00 !important;
  border-top: 0.1875rem solid var(--primary-orange) !important;
}

@media (max-width: 768px) {
  .navbar-brand {
    width: 7rem;
  }
}

:root {
  --primary-white: #fff;
  --primary-orange: #f9ab00;
  --primary-dark: rgba(255, 255, 255, 0);
  --primary-red: #ef4035;
  --primary-hover-red: #f33501;
}

.header-wrapper {
  position: relative;
  background: url(/static/media/bkg4.d6b2cafc.gif) no-repeat;
  background-color: rgba(0, 0, 0, 1);
  background-size: contain;
  background-position: center;
  height: 100vh;
}
#color-overlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: black;
  opacity: 0.8;
}

.main-info {
  display: flex;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
  z-index: 2;
}

.main-info h1 {
  /* color: var(--primary-orange);
  text-shadow: 0.5px 0.5px rgb(250, 250, 250); */
  color: #fff;
  color: var(--primary-white);
  font-size: 60px;
}
.main-info h7 {
  margin-top: 30px;
  color: #fff;
  color: var(--primary-white);
  /* text-shadow: 0.5px 0.5px rgb(250, 250, 250); */
}

.typed-text {
  font-size: 1rem;
  color: #fff;
  color: var(--primary-white);
}

@media (max-width: 768px) {
  .main-info h1 {
    font-size: 1.5rem !important;
  }
  .typed-text {
    font-size: 1.3rem !important;
  }
}

:root {
